<template>
    <view>
        <view class="newHead">
           <Head :headTitle="headTitle" :headOtherIcon="headOtherIcon" @otherClick="otherClick" :headReturn="headReturn"
                :newsIcon="newsIcon" :have='have' />
        </view>
        <view :class="[{otherBannerClass:otherModel},'bannerBox']" :style="[{height:otherModel ? '392rpx' :'314.58rpx','background-image':otherModel ?'url(../../static/banner1.jpg)' : ' url(../../static/banner.jpg)'}]">
            <swiper :indicator-dots="true" :autoplay="true" :circular="true" :interval="3000" :duration="1000">
                <!-- :key="item.image_id" -->
                <swiper-item v-for="item in bannerList" :key="item.index">
                    <image class="swiper-item" :src="item.image.file_path" mode=""></image>
                </swiper-item>
            </swiper>
        </view>
        <!-- 用户数 -->
        <view :class="[{otherUserStatisticsClass:otherModel},'userStatistics']">
            <view class="userText">
                <text class="iconfont icon-tongji"></text>
                <text class="user">用户数</text>
            </view>
            <view class="count">
                <text class="countText">
                    累积总数
                </text>
                <text class="countNum">{{pointCountInfo.total_count}}</text>
            </view>
            <view class="userClass">
                <view class="userClass_" @click="otherClick('/pages/list/node')">
                    <view class="userClass_name">
                        <text>节点数 </text>
                        <text class="iconfont icon-you you"></text>
                    </view>
                    <text class="nodeNum">{{pointCountInfo.jie}}</text>
                </view>
                <view class="userClass_" @click="otherClick('/pages/list/shop')">
                    <view class="userClass_name">
                        <text>商家数 </text>
                        <text class="iconfont icon-you you"></text>
                    </view>
                    <text class="shopNum">{{pointCountInfo.business}}</text>
                </view>
                <view class="userClass_" @click="otherClick('/pages/list/agent')">
                    <view class="userClass_name">
                        <text>合伙人数 </text>
                        <text class="iconfont icon-you you"></text>
                    </view>
                    <text class="agentNum">{{pointCountInfo.partner}}</text>
                </view>
            </view>
        </view>
        <!-- 交易数 -->
        <view :class="[{otherTransactionClass:otherModel},'transaction']" @click="otherClick('/pages/index/myIncome')">
            <view class="transactionUserText">
                <text class="iconfont icon-tongji"></text>
                <text class="user">交易数</text>
            </view>
            <view class="total">
                <view class="GMV">
                    <image src="/static/statisticsImg.png"></image>
                    <view class="total-content">
                        <text class="totalRevenue">
                            <text>GMV</text>
                        </text>                  
                        <text class="totalNum">{{pointBusCount.gmv | numFilter}}</text>
                    </view>
                </view>
                <view class="commission">
                    <image src="/static/commissionImg.png"></image>
                    <view class="total-content">
                        <text class="totalRevenue">
                            <text>总佣金</text>
                        </text>
                        <!-- <text class="totalNum commissionTotalNum">{{pointBusCount.commission_total + pointBusCount.jd_money | numFilter}}</text> -->
                        <text class="totalNum commissionTotalNum">{{pointBusCount.total_money | numFilter}}</text>
                    </view>
                </view>
                <view class="commission">
                    <image src="/static/index_leijishouru.png"></image>
                    <view class="total-content">
                        <text class="totalRevenue">
                            <text>累计收入</text>
                        </text>
                        <text class="totalNum sum">{{pointBusCount.user_total_money | numFilter}}</text>
                    </view>
                </view>
            </view>
            <!-- <view class="transactionCount">
                <view>
                    <image class="goldcoin" src="/static/goldcoin.png" mode=""></image>
                    <text class="transactionCountText">累积收入 <text class="transactionCountNumber">{{pointBusCount.user_total_money | numFilter}}</text></text>
                </view>
                <view class="transactionCountLine"></view>
            </view> -->
            <!-- <view class="incomeBox">
                <view class="income_">
                    <text>直推节点</text>
                    <text class="agentNum">{{pointBusCount.jd_money | numFilter}}</text>
                </view>
                <view class="income_">
                    <text>直推商家</text>
                    <text class="agentNum">{{pointBusCount.dirBus | numFilter}}</text>
                </view>
                <view class="income_">
                    <text>间推商家</text>
                    <text class="agentNum">{{pointBusCount.indirBus | numFilter}}</text>
                </view>
                <view class="income_">
                    <text>直推合伙人</text>
                    <text class="agentNum">{{pointBusCount.dirPart ? pointBusCount.dirPart : 0 | numFilter}}</text>
                </view>
                <view class="income_">
                    <text>间推合伙人</text>
                    <text class="agentNum">{{pointBusCount.indirPart ? pointBusCount.indirPart : 0 | numFilter}}</text>
                </view>
                <view class="income_">
                    <text>订单交易</text>
                    <text class="agentNum">{{pointBusCount.orderCommission | numFilter}}</text>
                </view>
            </view> -->
        </view>
        <!-- 我要推广 -->
       <view @click="closeMark" class="extension"></view>
        <view class="extensionBox" v-show="isMark">
            <view class="text">
                量子纠缠
                <text class="iconfont icon-guanbi1" @click="closeMark"></text>
            </view>
            <view class="extensionClass">
                <text class="extensionClass_" @click="extensionClick('node')"><text class="iconfont icon-lianjie"></text>节点</text>
                <text class="extensionClass_" @click="extensionClick('shop')"><text class="iconfont icon-lianjie"></text>商家</text>
                <text class="extensionClass_" @click="extensionClick('agent')"><text class="iconfont icon-lianjie"></text>合伙人</text>
            </view>
        </view>
        <view class="mark" v-show="isMark" @click="closeMark"></view>
    </view>
</template>
<script>
    import {
        httpRequest_,
        otherAPI
    } from 'pages/common/API.js';
    import Head from '../common/headhome.vue'
    export default {
        components: {
            Head
        },
        data() {
            return {
                // head               
                headTitle: {
                    title: '憨猴OS',
                    color: '#FFF',
                    size: '35.93rpx'
                },
                headOtherIcon: {
                    color: '#FFFFFF',
                    icon: 'icon-wode',
                    size: '45rpx',
                    url: '/pages/my/index'
                },
                headReturn: {
                    status: 'isShow',
                    color: 'white'
                },
                newsIcon: {
                    color: '#FFFFFF',
                    icon: 'icon-wode',
                    size: '45rpx',
                    url: '/pages/news/list'
                },

                have: true,
                bannerList: [],
                pointCountInfo: [],
                pointBusCount: {},
                jiaPointBusCount: {
                    total_GMV: 163333.00,
                    total_commission: 14584.00,
                    tatal_money: 1234567.00,
                    jie: 9000,
                    direct: 5000,
                    indirect: 5000
                },
                // 推广
                isMark: false,
                otherModel: false,                  
            }
        },      
         onLoad() {
             const data = uni.getStorageSync('data').token
         },
        onShow() {
            const data = uni.getStorageSync('data').token                
            if (data) {
                this.banner();
                this.getpointCount();
                this.getpointBusCount();
                // 获取是否有未读信息
                this.getMsg()
                if (uni.getSystemInfoSync().windowHeight > 680) {
                    this.otherModel = true
                }
            }          
        },
        methods: {
            banner() {
                let url = `/api/user/slide`;
                let obj = {
                    token: uni.getStorageSync('data').token
                }
                httpRequest_.get(url, obj).then(res => {
                    this.bannerList = res.data.data
                })
            },
            getpointCount() {
                let url = `/api/user_relation/pointUserCount`;
                let obj = {
                    token: uni.getStorageSync('data').token
                }
                httpRequest_.get(url, obj).then(res => {                  
                    if (res.code == 1) {
                        this.pointCountInfo = res.data
                    } else {
                        otherAPI.toast(res.msg, 'none', false, 1500);
                    }
                })
            },
            //这个接口是我的交易接口
            getpointBusCount() {
                let url = `/api/user_relation/gmv`;
                let obj = {
                    token: uni.getStorageSync('data').token
                }
                httpRequest_.get(url, obj).then(res => {                 
                    if (res.code == 1) {
                        this.pointBusCount = res.data.data
                    } else {
                        otherAPI.toast(res.msg, 'none', false, 1500);
                    }
                });
            },
            closeMark() {
                this.isMark = !this.isMark;
            },
            extensionClick(param) {
                if (getApp().globalData.navStatus === true) {
                    return false
                }
                getApp().globalData.navStatus = true;
                uni.navigateTo({
                    url: '/pages/extension/index?param=' + param + '',
                    success: res => {
                        this.isMark = !this.isMark;
                        getApp().globalData.navStatus = false;
                    }
                });
            },
            otherClick(url) {        
                otherAPI.nav(url);
            },
            getMsg() {
                let url = `/api/operation.message/message_type_one_show`;
                let obj = {
                    token: uni.getStorageSync('data').token
                }
                httpRequest_.get(url, obj).then(res => {
                    if (res.code == 1) {
                        this.have = true
                    } else {
                        this.have = false
                    }
                });
            }
        }
    }
</script>
<style>
    @import url("../../pages/common/iconfont.css");

    page {
        width: 100%;
        height: 100%;
        /*以前的首页背景图../../static/index,因为量子纠缠按钮位置调整,导致背景图没有纹理,index2.jpg是从新切的背景图  */
        background-image: url(../../static/index2.jpg) !important;
        background-size: 100% 100%;
        
    }

    /* newHead固定 */
    .newHead {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 128rpx;
        background-color: #000b1b;
        z-index: 10000;
    }

    .bannerBox {
        width: 750rpx;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        margin-top: 128rpx;
        position: relative;
    }

    .bannerBox swiper {
        margin: auto;
        width: 520rpx;
        height: 240rpx;
        position: relative;
        top: 35rpx;
    }

    .bannerBox swiper image {
        width: 100%;
        height: 100%;
    }

    .otherBannerClass swiper {
        margin: auto;
        width: 530rpx;
        height: 245rpx;
