    }

    .userStatistics {
        width: 586.11rpx;
        height: 322.22rpx;
        margin: auto;
        background-image: url(../../static/user.png);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: relative;
    }

    .userText {
        position: relative;
        top: 20rpx;
        left: 35rpx;
    }

    .userText .user {
        margin-left: 10.41rpx;
        color: #dee1ef;
        font-size: 27.77rpx;
    }

    .icon-tongji {
        /* 用户数/交易数左边的图标大小[UI需求跟右边文字一样大] */
        font-size: 24rpx;
        color: #284289;
    }

    .count {
        margin: 20rpx auto;
        width: 510.41rpx;
        height: 100rpx;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .count .countText {
        color: #4f7ec9;
        font-size: 25rpx;
    }

    .count .countNum {
        color: #e4992d;
        font-size: 69.44rpx;
        display: inline-block;
        line-height: 70rpx;
    }

    .userClass {
        margin: 10rpx auto 0;
        width: 416.66rpx;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .userClass .userClass_ {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .userClass .userClass_ .userClass_name {
        color: #8691be;
        font-size: 26rpx
    }

    .userClass_name {
        display: flex;
        align-items: center;
    }

    .userClass_name .you {
        width: 0;
    }

    /* 交易数背景 */
    .transaction {
        /* 293/240 */
        width: 586.11rpx;
        height: 426rpx;
        margin: auto;
        margin-top: 30rpx;
        /* background-color: #000308; */
        background-image: url(../../static/transaction2.png);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        position: relative;
    }

    .transactionUserText {
        position: absolute;
        top: 20rpx;
        left: 50%;
        transform: translateX(-50%);
        width: 510.41rpx;
        line-height: 69.44rpx;
    }

    .transactionUserText .user {
        margin-left: 10.41rpx;
        color: #dee1ef;
        font-size: 27.77rpx;
    }

    .total {
        margin: auto;
        padding-top: 70rpx;
        width: 510.41rpx;
        display: flex;
        flex-flow: column;
        padding-left: 75rpx;
        /* 
        justify-content: space-around;
        align-items: center; */
    }

    .total .totalNum {
