    }

    .extension {
        width: 750rpx;
        height: 180rpx;
        /* position: absolute;
        bottom: 50rpx;
        left: 50%;
        transform: translateX(-50%); */
        background-image: url(../../static/share.jpg) !important;
        background-size: 100% 100%;
        /* margin: 150rpx auto 100rpx; */
        margin: 100rpx auto 50rpx;
    }

    .extensionBox {
